import React, { useState, useEffect } from 'react';
import { Actor, HttpAgent } from '@dfinity/agent';
import { idlFactory as backend_idl, canisterId as backend_id } from './declarations/backend'; // Importa las declaraciones

import logo from './logo.svg';
import './App.css';

const agent = new HttpAgent();
const backend = Actor.createActor(backend_idl, { agent, canisterId: backend_id });

function App() {
  const [principal, setPrincipal] = useState('');

  useEffect(() => {
    // Llama a una funcin del backend (por ejemplo `whoAmI`)
    backend.whoAmI().then((returnedPrincipal: unknown) => {
      setPrincipal(returnedPrincipal as string);  // Actualiza el estado con el valor devuelto
      console.log(principal);
    }).catch((error) => {
      console.error("Error calling whoAmI:", error);  // Manejo de errores
    });
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.tsx</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

export default App;
