export const idlFactory = ({ IDL }) => {
  const CompanyId = IDL.Nat;
  const CardDataInit = IDL.Record({
    'name' : IDL.Text,
    'profession' : IDL.Text,
    'email' : IDL.Text,
    'skils' : IDL.Vec(IDL.Text),
    'phone' : IDL.Nat,
    'photo' : IDL.Vec(IDL.Nat8),
    'positions' : IDL.Vec(
      IDL.Record({ 'possition' : IDL.Text, 'company' : CompanyId })
    ),
    'photoPreview' : IDL.Vec(IDL.Nat8),
  });
  const CardPublicData = IDL.Record({
    'owner' : IDL.Principal,
    'name' : IDL.Text,
    'profession' : IDL.Text,
    'contactQty' : IDL.Nat,
    'score' : IDL.Nat,
    'skils' : IDL.Vec(IDL.Text),
    'rewiews' : IDL.Vec(IDL.Text),
    'photo' : IDL.Vec(IDL.Nat8),
    'positions' : IDL.Vec(
      IDL.Record({ 'possition' : IDL.Text, 'company' : CompanyId })
    ),
  });
  const CompanyInit = IDL.Record({
    'ceo' : IDL.Text,
    'thumbnailPhotoCeo' : IDL.Vec(IDL.Nat8),
    'foundedYear' : IDL.Nat,
    'logo' : IDL.Vec(IDL.Nat8),
    'name' : IDL.Text,
    'description' : IDL.Text,
    'photoCeo' : IDL.Vec(IDL.Nat8),
    'email' : IDL.Text,
    'website' : IDL.Opt(IDL.Text),
    'thumbnailLogo' : IDL.Vec(IDL.Nat8),
    'phone' : IDL.Nat,
    'socialNetworks' : IDL.Vec(IDL.Text),
    'location' : IDL.Text,
    'industry' : IDL.Text,
  });
  const Id = IDL.Nat;
  const CreateResult = IDL.Variant({ 'Ok' : Id, 'Err' : IDL.Text });
  const CompleteCardData = IDL.Record({
    'owner' : IDL.Principal,
    'name' : IDL.Text,
    'profession' : IDL.Text,
    'contactQty' : IDL.Nat,
    'email' : IDL.Text,
    'score' : IDL.Nat,
    'skils' : IDL.Vec(IDL.Text),
    'rewiews' : IDL.Vec(IDL.Text),
    'phone' : IDL.Nat,
    'photo' : IDL.Vec(IDL.Nat8),
    'positions' : IDL.Vec(
      IDL.Record({ 'possition' : IDL.Text, 'company' : CompanyId })
    ),
  });
  const CardPreview = IDL.Record({
    'owner' : IDL.Principal,
    'name' : IDL.Text,
    'profession' : IDL.Text,
    'skils' : IDL.Vec(IDL.Text),
    'positions' : IDL.Vec(
      IDL.Record({ 'possition' : IDL.Text, 'company' : CompanyId })
    ),
    'photoPreview' : IDL.Vec(IDL.Nat8),
  });
  const GetPublicCardsResult = IDL.Variant({
    'Ok' : IDL.Record({
      'cardsPreview' : IDL.Vec(CardPreview),
      'thereIsMore' : IDL.Bool,
    }),
    'Err' : IDL.Text,
  });
  const UpdatableData = IDL.Record({
    'email' : IDL.Text,
    'skils' : IDL.Vec(IDL.Text),
    'porfession' : IDL.Text,
    'phone' : IDL.Nat,
  });
  return IDL.Service({
    'createCard' : IDL.Func(
        [CardDataInit],
        [IDL.Variant({ 'Ok' : CardPublicData, 'Err' : IDL.Text })],
        [],
      ),
    'createCardFor' : IDL.Func(
        [CardDataInit, IDL.Principal],
        [IDL.Variant({ 'Ok' : CardPublicData, 'Err' : IDL.Text })],
        [],
      ),
    'createCompany' : IDL.Func([CompanyInit], [CreateResult], []),
    'getCompleteDataCard' : IDL.Func(
        [IDL.Principal],
        [IDL.Variant({ 'Ok' : CompleteCardData, 'Err' : IDL.Text })],
        ['query'],
      ),
    'getContactRequests' : IDL.Func(
        [],
        [IDL.Variant({ 'Ok' : IDL.Vec(IDL.Principal), 'Err' : IDL.Text })],
        ['query'],
      ),
    'getMyCard' : IDL.Func([], [IDL.Opt(CompleteCardData)], ['query']),
    'getMyContacts' : IDL.Func([], [IDL.Vec(IDL.Principal)], ['query']),
    'getPaginatePublicCards' : IDL.Func(
        [IDL.Nat],
        [GetPublicCardsResult],
        ['query'],
      ),
    'getPublicDataCard' : IDL.Func(
        [IDL.Principal],
        [IDL.Variant({ 'Ok' : CardPublicData, 'Err' : IDL.Text })],
        ['query'],
      ),
    'setUpdateLockTime' : IDL.Func([IDL.Int], [IDL.Int], []),
    'shareCard' : IDL.Func(
        [IDL.Principal],
        [IDL.Variant({ 'Ok' : IDL.Null, 'Err' : IDL.Text })],
        [],
      ),
    'updateCard' : IDL.Func(
        [UpdatableData],
        [IDL.Variant({ 'Ok' : CardPublicData, 'Err' : IDL.Text })],
        [],
      ),
    'whoAmI' : IDL.Func([], [IDL.Text], ['query']),
  });
};
export const init = ({ IDL }) => { return []; };
